import Vue from 'vue'
import Router from 'vue-router'
import vHome from './views/vHome.vue'
import vAbout from './views/vAbout.vue'
import vSingup from './views/vSingup.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: vHome
    },
    {
      path: '/about',
      name: 'about',
      component: vAbout
    },
    {
      path: '/singup',
      name: 'singup',
      component: vSingup
    }
  ]
})
